.advantages {
  padding-top: 120px;
  padding-bottom: 60px;

  @include vp-1023 {
    padding-top: 80px;
    padding-bottom: 74px;
  }

  @include vp-767 {
    padding-top: 50px;
    padding-bottom: 34px;
  }
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;

  @include vp-1023 {
    column-gap: 29px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-template-rows: 340px 340px 325px 325px;
    grid-gap: 30px;
  }
}

.advantages__item {
  border: 1px solid #ebebeb;
  border-left: 5px solid $deep-sky-blue;
  padding: 40px 45px 35px 42px;
  min-height: 300px;

  @include vp-1023 {
    min-height: 360px;
    padding: 39px 40px 30px;
  }

  @include vp-767 {
    padding: 31px 25px 35px 25px;
    min-height: auto;
    max-width: 375px;
    overflow: auto;
    margin-right: auto;
    margin-left: auto;
  }
}

.advantages__item h3 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 13px;
  color: $deep-sky-blue;

  @include vp-1023 {
    margin-bottom: 6px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 8px;
  }
}

.advantages p {
  margin: 0;
  padding: 0;

  @include vp-1023 {
    font-size: 16px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages svg {
  fill: $deep-sky-blue;
  width: 35px;
  height: 35px;
  margin-bottom: 15px;

  @include vp-1023 {
    margin-bottom: 10px;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;
  }
}

.advantages__item:hover {
  background-color: $deep-sky-blue;
  color: $alice-blue;
}

.advantages__item:hover svg {
  fill: $alice-blue;
}

.advantages__item:hover h3 {
  color: $alice-blue;
}
