.logo,
.page-footer__logo {
  content: "";
  display: block;
  grid-area: logo;
  width: 215px;
  height: 55px;
  background: url("../img/logo.svg");
  background-size: 100%;
}

.logo {
  @include vp-767 {
    background: url("../img/logo-mobile.svg");
    width: 35px;
    height: 35px;
    background-size: 100%;
  }
}

.page-footer__logo {
  @include vp-767 {
    background: url("../img/logo.svg");
    background-size: 100%;
  }
}
